import React from 'react';
import {If, Then} from 'react-if';

import Btn from '../btn/btn';
import MerchStrip from '../merch-strip/merch-strip';
import ChannelModal from '../channel-modal/channel-modal';

import maxImage from '../../images/packages/max.png';
import entertainmentImage from '../../images/packages/entertainment.png';
import starterImage from '../../images/packages/starter.png';

import './product-card.scss';

const ProductCard = (props) => {
  const packageOneMerchStrip = props.packageOneMerchStrip;
  const packageOneImage = props.packageOneImage;
  const packageOneTitle = props.packageOneTitle;
  const packageOneChannelNumber = props.packageOneChannelNumber;
  const packageOneSalesMessage = props.packageOneSalesMessage;
  const packageOneBpOne = props.packageOneBpOne;
  const packageOneBpTwo = props.packageOneBpTwo;
  const packageOneBpThree = props.packageOneBpThree;
  const packageOneBpFour = props.packageOneBpFour;
  const packageOneBpFive = props.packageOneBpFive;
  const packageOneBpSix = props.packageOneBpSix;
  const packageOnePrice = props.packageOnePrice;
  const packageOneStandardPrice = props.packageOneStandardPrice;
  const packageOneContract = props.packageOneContract;
  const packageOneCtaText = props.packageOneCtaText;
  const packageOneCtaLink = props.packageOneCtaLink;

  const packageTwoMerchStrip = props.packageTwoMerchStrip;
  const packageTwoImage = props.packageTwoImage;
  const packageTwoTitle = props.packageTwoTitle;
  const packageTwoChannelNumber = props.packageTwoChannelNumber;
  const packageTwoSalesMessage = props.packageTwoSalesMessage;
  const packageTwoBpOne = props.packageTwoBpOne;
  const packageTwoBpTwo = props.packageTwoBpTwo;
  const packageTwoBpThree = props.packageTwoBpThree;
  const packageTwoBpFour = props.packageTwoBpFour;
  const packageTwoBpFive = props.packageTwoBpFive;
  const packageTwoBpSix = props.packageTwoBpSix;
  const packageTwoPrice = props.packageTwoPrice;
  const packageTwoStandardPrice = props.packageTwoStandardPrice;
  const packageTwoContract = props.packageTwoContract;
  const packageTwoCtaText = props.packageTwoCtaText;
  const packageTwoCtaLink = props.packageTwoCtaLink;

  return(
    <section className="card">
      <MerchStrip text={packageOneMerchStrip} />
      <MerchStrip text={packageTwoMerchStrip} />
      <div className="card__details">
        <h2 className="card__title">
          {packageOneTitle}
          {packageTwoTitle}
        </h2>
        <h3 className="card__channel-number h4">
          {packageOneChannelNumber}
          {packageTwoChannelNumber}
        </h3>
        <ChannelModal />
        <If condition={packageOneImage === 'max' || packageTwoImage === 'max'}>
            <Then>
              <img
                className="card__channel-image"
                alt="Channels available on your package"
                src={maxImage}
              />
            </Then>
        </If>
        <If condition={packageOneImage === 'entertainment' || packageTwoImage === 'entertainment'}>
            <Then>
              <img
                className="card__channel-image"
                alt="Channels available on your package"
                src={entertainmentImage}
              />
            </Then>
        </If>
        <If condition={packageOneImage === 'starter' || packageTwoImage === 'starter'}>
          <img
            className="card__channel-image"
            alt="Channels available on your package"
            src={starterImage}
          />
        </If>
      </div>
      <div className="card__benefits">
        <p className="h3">
          {packageOneSalesMessage}
          {packageTwoSalesMessage}
        </p>
        <ul className="card__list">
          <li>
            {packageOneBpOne}
            {packageTwoBpOne}
          </li>
          <li>
            {packageOneBpTwo}
            {packageTwoBpTwo}
          </li>
          <li>
            {packageOneBpThree}
            {packageTwoBpThree}
          </li>
          <If condition={packageOneBpFour || packageTwoBpFour}>
            <li>
              {packageOneBpFour}
              {packageTwoBpFour}
            </li>
          </If>
          <If condition={packageOneBpFive || packageTwoBpFive}>
            <li>
              {packageOneBpFive}
              {packageTwoBpFive}
            </li>
          </If>
          <If condition={packageOneBpSix || packageTwoBpSix}>
            <li>
              {packageOneBpSix}
              {packageTwoBpSix}
            </li>
          </If>
        </ul>
      </div>
      <div className="card__pricing">
        <p>
          Now <span className="card__pricehighlight">&pound;
          {packageOnePrice}
          {packageTwoPrice}
          </span> a month
        </p>
        <p>
          {packageOneContract}
          {packageTwoContract}
        </p>
        <p>Price after&nbsp;
          {packageOneContract}
          {packageTwoContract}&nbsp;&pound;
          {packageOneStandardPrice}
          {packageTwoStandardPrice}
        </p>
        <Btn
          href={packageOneCtaLink}
          className="btn btn--purchase"
          text={packageOneCtaText}
        />
        <Btn
          href={packageTwoCtaLink}
          className="btn btn--purchase"
          text={packageTwoCtaText}
        />
      </div>
    </section>
  )
};

export default ProductCard;
