/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import Helmet from "react-helmet"
import { useStaticQuery, graphql } from "gatsby"



function SEO({ description, lang, meta, keywords, title }) {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
          }
        }
      }
    `
  )

  const metaDescription = description || site.siteMetadata.description

  const checkLoadDante = ({ scriptTags }) => {
    const RXP_JQUERY = /jquery/;

    if (!scriptTags) {
      return;
    }

    scriptTags.forEach((tag) => {
      if (!RXP_JQUERY.test(tag.src)) {
        return;
      }

      tag.onload = function() {
        const danteHeader = document.createElement('script');
        const danteFooter = document.createElement('script');

        danteHeader.src = 'https://img01.bt.co.uk/s/assets/js/dantenewgh.api-1.0.js';
        danteHeader.onload = function() {
          window.DanteNewGH.init({
            siteArea: 'presales'
          });
        };
        danteFooter.src = 'https://img01.bt.co.uk/s/assets/js/dantegf.api-1.0.js';
        danteFooter.onload = function() {
          window.DanteGF.init({});
        };
        document.body.appendChild(danteHeader);
        document.body.appendChild(danteFooter);
      };
    });
  }

  return (
    <Helmet
      htmlAttributes={{
        lang
      }}
      script={[
        {
          src:
            "https://cdnjs.cloudflare.com/ajax/libs/jquery/1.9.0/jquery.min.js",
          type: "text/javascript"
        }
      ]}
      onChangeClientState={(newState, addedTags) => checkLoadDante(addedTags)}
      title={title}
      titleTemplate={`${site.siteMetadata.title}`}
      meta={[
        {
          name: `description`,
          content: metaDescription
        },
        {
          property: `og:title`,
          content: title
        },
        {
          property: `og:description`,
          content: metaDescription
        },
        {
          property: `og:type`,
          content: `website`
        },
        {
          name: `twitter:card`,
          content: `summary`
        },
        {
          name: `twitter:title`,
          content: title
        },
        {
          name: `twitter:description`,
          content: metaDescription
        }
      ]
        .concat(
          keywords.length > 0
            ? {
                name: `keywords`,
                content: keywords.join(`, `)
              }
            : []
        )
        .concat(meta)}
    />
  );
}

SEO.defaultProps = {
  lang: `en`,
  meta: [],
  keywords: [],
  description: ``,
}

SEO.propTypes = {
  description: PropTypes.string,
  lang: PropTypes.string,
  meta: PropTypes.arrayOf(PropTypes.object),
  keywords: PropTypes.arrayOf(PropTypes.string),
  title: PropTypes.string.isRequired,
}

export default SEO
